/**
 * PayOrc SDK - A lightweight client-side SDK for integrating payment processing
 */

interface ModalStyles {
  width?: string;
  maxWidth?: string;
  height?: string;
  maxHeight?: string;
  backgroundColor?: string;
  borderRadius?: string;
  boxShadow?: string;
  padding?: string;
  margin?: string;
  zIndex?: string;
  overlayColor?: string;
  mobileWidth?: string;
  mobileHeight?: string;
  tabletWidth?: string;
  tabletHeight?: string;
}

interface PaymentConfig {
  apiKey: string;
  merchantSecret: string;
  environment?: 'test' | 'production';
  onSuccess?: (response: any) => void;
  onFailure?: (error: any) => void;
  onCancel?: () => void;
  modalStyles?: ModalStyles;
}

interface OrderDetails {
  m_order_id: string;
  amount: string;
  convenience_fee: string;
  currency: string;
  description?: string;
  quantity: string;
}

interface CustomerDetails {
  name: string;
  m_customer_id: string;
  email: string;
  mobile?: string;
  code?: string;
}

interface BillingDetails {
  address_line1: string;
  address_line2?: string;
  city: string;
  province: string;
  pin: string;
  country: string;
}

interface ShippingDetails {
  shipping_name: string;
  shipping_email?: string;
  shipping_code?: string;
  shipping_mobile?: string;
  address_line1: string;
  address_line2?: string;
  city: string;
  province: string;
  pin: string;
  country: string;
  location_pin?: string;
  shipping_currency: string;
  shipping_amount: string;
}

interface UrlDetails {
  success?: string;
  cancel?: string;
  failure?: string;
}

interface KeyValuePair {
  [key: string]: string;
}

interface PaymentData {
  class: 'ECOM';
  action: 'SALE';
  capture_method: 'AUTOMATIC';
  payment_token?: string;
  order_details: OrderDetails;
  customer_details: CustomerDetails;
  billing_details: BillingDetails;
  shipping_details: ShippingDetails;
  urls?: UrlDetails;
  parameters?: KeyValuePair[];
  custom_data?: KeyValuePair[];
}

interface PaymentRequest {
  data: PaymentData;
}

interface PaymentResponse {
  status: string;
  status_code: string;
  message: string;
  p_order_id: number;
  m_order_id: string;
  p_request_id: number;
  order_creation_date: string;
  amount: string;
  payment_link: string;
  iframe_link: string;
}

type PaymentEventType = 'success' | 'failure' | 'cancel' | 'processing';

interface PaymentEventListener {
  type: PaymentEventType;
  callback: (data?: any) => void;
}

class PaymentSDK {
  private static instance: PaymentSDK | null = null;
  private apiKey: string = '';
  private merchantSecret: string = '';
  private environment: 'test' | 'production' = 'test';
  private baseUrl: string = 'https://nodeserver.payorc.com/api/v1';
  private mountElement: HTMLElement | null = null;
  private iframe: HTMLIFrameElement | null = null;
  private modal: HTMLDivElement | null = null;
  private eventListeners: PaymentEventListener[] = [];
  private config: PaymentConfig | null = null;
  private defaultModalStyles: ModalStyles = {
    width: '90%',
    maxWidth: '600px',
    height: 'auto',
    maxHeight: '90vh',
    backgroundColor: '#ffffff',
    borderRadius: '8px',
    boxShadow: '0 4px 12px rgba(0, 0, 0, 0.15)',
    padding: '0',
    margin: '20px',
    zIndex: '9999',
    overlayColor: 'rgba(0, 0, 0, 0.5)',
    mobileWidth: '95%',
    mobileHeight: '95vh',
    tabletWidth: '90%',
    tabletHeight: '90vh'
  };

  private constructor() {
    window.addEventListener('message', this.handlePostMessage.bind(this));
  }

  public static getInstance(): PaymentSDK {
    if (!PaymentSDK.instance) {
      PaymentSDK.instance = new PaymentSDK();
    }
    return PaymentSDK.instance;
  }

  public init(config: PaymentConfig): PaymentSDK {
    this.apiKey = config.apiKey;
    this.merchantSecret = config.merchantSecret;
    this.environment = config.environment || 'test';
    this.config = config;
    
    if (config.modalStyles) {
      this.defaultModalStyles = {
        ...this.defaultModalStyles,
        ...config.modalStyles
      };
    }
    
    if (config.onSuccess) this.on('success', config.onSuccess);
    if (config.onFailure) this.on('failure', config.onFailure);
    if (config.onCancel) this.on('cancel', config.onCancel);
    
    return this;
  }

  public mount(elementId: string): PaymentSDK {
    this.mountElement = document.getElementById(elementId);
    if (!this.mountElement) {
      console.error(`Element with ID "${elementId}" not found`);
    }
    return this;
  }

  private getBrowserInfo(): { platform: string; browser: string; browserVersion: string } {
    const platform = navigator.platform || 'unknown';
    const userAgent = navigator.userAgent;
    let browser = 'unknown';
    let browserVersion = 'unknown';

    if (userAgent.indexOf('Chrome') !== -1) {
      browser = 'Chrome';
      const match = userAgent.match(/Chrome\/(\d+\.\d+)/);
      if (match) browserVersion = match[1];
    } else if (userAgent.indexOf('Firefox') !== -1) {
      browser = 'Firefox';
      const match = userAgent.match(/Firefox\/(\d+\.\d+)/);
      if (match) browserVersion = match[1];
    } else if (userAgent.indexOf('Safari') !== -1) {
      browser = 'Safari';
      const match = userAgent.match(/Version\/(\d+\.\d+)/);
      if (match) browserVersion = match[1];
    } else if (userAgent.indexOf('Edge') !== -1 || userAgent.indexOf('Edg') !== -1) {
      browser = 'Edge';
      const match = userAgent.match(/Edge?\/(\d+\.\d+)/);
      if (match) browserVersion = match[1];
    } else if (userAgent.indexOf('Opera') !== -1 || userAgent.indexOf('OPR') !== -1) {
      browser = 'Opera';
      const match = userAgent.match(/OPR\/(\d+\.\d+)/);
      if (match) browserVersion = match[1];
    }

    return { platform, browser, browserVersion };
  }

  public async createPayment(paymentData: PaymentData, displayMode: 'modal' | 'iframe' | 'hosted' = 'modal'): Promise<PaymentResponse | null> {
    if (!this.apiKey || !this.merchantSecret) {
      throw new Error('SDK not initialized. Call init() first.');
    }

    try {
      const paymentRequest: PaymentRequest = { data: paymentData };
      const { platform, browser, browserVersion } = this.getBrowserInfo();

      const response = await fetch(`${this.baseUrl}/sdk/orders/create`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'merchant-key': this.apiKey,
          'merchant-secret': this.merchantSecret,
          'platform': platform,
          'browser': browser,
          'browser-version': browserVersion
        },
        body: JSON.stringify(paymentRequest)
      });

      const responseData: PaymentResponse = await response.json();

      if (responseData.status !== 'SUCCESS') {
        this.triggerEvent('failure', responseData);
        return null;
      }

      switch (displayMode) {
        case 'hosted':
          window.location.href = responseData.payment_link;
          break;
        case 'iframe':
          this.loadIframe(responseData.iframe_link);
          break;
        case 'modal':
          this.loadModal(responseData.iframe_link);
          break;
      }

      this.triggerEvent('processing', responseData);
      return responseData;
    } catch (error) {
      console.error('Error creating payment:', error);
      this.triggerEvent('failure', { error });
      return null;
    }
  }

  public on(eventType: PaymentEventType, callback: (data?: any) => void): PaymentSDK {
    this.eventListeners.push({ type: eventType, callback });
    return this;
  }

  public off(eventType: PaymentEventType, callback: (data?: any) => void): PaymentSDK {
    this.eventListeners = this.eventListeners.filter(
      listener => !(listener.type === eventType && listener.callback === callback)
    );
    return this;
  }

  public close(): void {
    if (this.modal) {
      document.body.removeChild(this.modal);
      this.modal = null;
    }
    
    if (this.iframe && this.mountElement) {
      this.mountElement.removeChild(this.iframe);
      this.iframe = null;
    }
  }

  private handlePostMessage(event: MessageEvent): void {
    try {
      const data = typeof event.data === 'string' ? JSON.parse(event.data) : event.data;
      
      if (data.status === "SUCCESS") {
        this.triggerEvent('success', data);
        this.close();
      } else if (data.status === "CANCELLED") {
        this.triggerEvent('cancel', data);
        this.close();
      } else if (data.status === "FAILED") {
        this.triggerEvent('failure', data);
        this.close();
      }
    } catch (error) {
      console.error('Error processing message:', error);
    }
  }

  private triggerEvent(eventType: PaymentEventType, data?: any): void {
    this.eventListeners
      .filter(listener => listener.type === eventType)
      .forEach(listener => {
        try {
          listener.callback(data);
        } catch (error) {
          console.error(`Error in ${eventType} event listener:`, error);
        }
      });
  }

  private loadIframe(iframeUrl: string): void {
    if (!this.mountElement) {
      console.error('Mount element not found');
      return;
    }

    this.mountElement.innerHTML = '';
    this.iframe = document.createElement('iframe');
    this.iframe.src = iframeUrl;
    this.iframe.style.width = '100%';
    this.iframe.style.height = '600px';
    this.iframe.style.border = 'none';
    this.iframe.style.borderRadius = '8px';
    this.iframe.setAttribute('allowpaymentrequest', 'true');
    this.iframe.setAttribute('allow', 'payment');

    this.mountElement.appendChild(this.iframe);
  }

  private loadModal(iframeUrl: string): void {
    // Create modal container with overlay
    this.modal = document.createElement('div');
    Object.assign(this.modal.style, {
      position: 'fixed',
      top: '0',
      left: '0',
      width: '100%',
      height: '100%',
      backgroundColor: this.defaultModalStyles.overlayColor,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      zIndex: this.defaultModalStyles.zIndex
    });

    // Create modal content container
    const modalContent = document.createElement('div');
    Object.assign(modalContent.style, {
      width: this.defaultModalStyles.width,
      maxWidth: this.defaultModalStyles.maxWidth,
      height: this.defaultModalStyles.height,
      maxHeight: this.defaultModalStyles.maxHeight,
      backgroundColor: this.defaultModalStyles.backgroundColor,
      borderRadius: this.defaultModalStyles.borderRadius,
      boxShadow: this.defaultModalStyles.boxShadow,
      padding: this.defaultModalStyles.padding,
      margin: this.defaultModalStyles.margin,
      position: 'relative',
      overflow: 'hidden',
      display: 'flex',
      flexDirection: 'column'
    });

    // Create iframe container
    const iframeContainer = document.createElement('div');
    Object.assign(iframeContainer.style, {
      flex: '1',
      minHeight: '400px',
      overflow: 'hidden',
      width: '100%',
      height: '100%'
    });

    // Create and style iframe
    this.iframe = document.createElement('iframe');
    Object.assign(this.iframe.style, {
      width: '100%',
      height: '100%',
      border: 'none',
      display: 'block'
    });
    this.iframe.src = iframeUrl;
    this.iframe.setAttribute('allowpaymentrequest', 'true');
    this.iframe.setAttribute('allow', 'payment');

    // Add responsive styles
    const mediaQueries = `
      @media (max-width: 768px) {
        .payorc-modal-content {
          width: ${this.defaultModalStyles.mobileWidth};
          height: ${this.defaultModalStyles.mobileHeight};
          margin: 10px;
        }
      }
      @media (min-width: 769px) and (max-width: 1024px) {
        .payorc-modal-content {
          width: ${this.defaultModalStyles.tabletWidth};
          height: ${this.defaultModalStyles.tabletHeight};
        }
      }
    `;

    const styleSheet = document.createElement('style');
    styleSheet.textContent = mediaQueries;
    document.head.appendChild(styleSheet);

    modalContent.classList.add('payorc-modal-content');

    // Assemble modal
    iframeContainer.appendChild(this.iframe);
    modalContent.appendChild(iframeContainer);
    this.modal.appendChild(modalContent);

    // Prevent closing when clicking outside
    this.modal.addEventListener('click', (e) => {
      if (e.target === this.modal) {
        e.preventDefault();
        e.stopPropagation();
      }
    });

    document.body.appendChild(this.modal);
  }
}

export type {
  PaymentConfig,
  PaymentData,
  OrderDetails,
  CustomerDetails,
  BillingDetails,
  ShippingDetails,
  UrlDetails,
  PaymentResponse,
  PaymentEventType,
  ModalStyles
};

export default PaymentSDK;